import React from "react";
import styled from "styled-components";
import { Button, Divider, message, Modal } from "antd";
import CopyToClipboard from "react-copy-to-clipboard";

function Accordion(props) {
  const parentRef = React.useRef(null);
  const childRef = React.useRef(null);
  const [isCollapse, setIsCollapse] = React.useState(false);

  const handleButtonClick = React.useCallback(
    (event) => {
      event.stopPropagation();
      if (parentRef.current === null || childRef.current === null) {
        return;
      }
      if (parentRef.current.clientHeight > 0) {
        parentRef.current.style.height = "0";
        parentRef.current.style.background = "#efebe9";
      } else {
        parentRef.current.style.height = `${childRef.current.clientHeight}px`;
        parentRef.current.style.background = "#efebe9";
      }
      setIsCollapse(!isCollapse);
    },
    [isCollapse]
  );

  const parentRefHeight = parentRef.current?.style.height ?? "0px";
  const buttonText = parentRefHeight === "0px" ? "열기" : "닫기";

  return (
    <Container>
      <ContactButton data-aos="fade-up" onClick={handleButtonClick}>
        {props.title}
        <Button2>{buttonText}</Button2>
      </ContactButton>
      <ContentsWrapper ref={parentRef}>
        <div ref={childRef}>
          <div style={{ marginTop: 10 }}>
            <b>{props.mainName}</b>
            <br />
            <CopyToClipboard text={props.mainAccount}>
              <Button
                type="text"
                style={{ padding: 0, margin: 0 }}
                onClick={() => message.success("계좌번호가 복사되었습니다.")}
              >
                {props.mainAccount}
              </Button>
            </CopyToClipboard>
          </div>
          <div style={{ marginTop: 24, marginBottom: 24 }}>
            <b>{props.father}</b>
            <br />
            <CopyToClipboard text={props.fatherAccount}>
              <Button
                type="text"
                style={{ padding: 0, margin: 0 }}
                onClick={() => message.success("계좌번호가 복사되었습니다.")}
              >
                {props.fatherAccount}
              </Button>
            </CopyToClipboard>
          </div>
          <div style={{ marginBottom: 10 }}>
            <b>{props.mother}</b>
            <br />
            <CopyToClipboard text={props.motherAccount}>
              <Button
                type="text"
                style={{ padding: 0, margin: 0 }}
                onClick={() => message.success("계좌번호가 복사되었습니다.")}
              >
                {props.motherAccount}
              </Button>
            </CopyToClipboard>
          </div>
        </div>
      </ContentsWrapper>
    </Container>
  );
}

const Container = styled.div`
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: center;
`;

const ContactButton = styled(Button)`
  border-radius: 4px;
  display: flex;
  background: rgba(133, 131, 131, 0.2);
  border-color: rgba(133, 131, 131, 0.2) !important;
  color: var(--title-color) !important;
  font-weight: 400 !important;
  align-item: center;
  cursor: pointer;
  width: 100%;
  height: 32px;
  &:hover {
    background: rgb(133 131 131) !important;
    border-color: rgb(133 131 131) !important;
    color: var(--title-color) !important;
  }
`;

const Button2 = styled.div`
  top: 6px;
  right: 8px;
  font-size: 14px;
  position: absolute;
`;

const ContentsWrapper = styled.div`
  height: 0;
  width: inherit;
  padding: 8 8px;
  overflow: hidden;
  transition: height 0.35s ease, background 0.35s ease;
`;

export default React.memo(Accordion);
