import React, { useEffect } from "react";
import { Divider } from "antd";
import styled from "styled-components";
import "../styles/gallery.css";

import Photo1 from "../assets/001.jpg";
import Photo2 from "../assets/002.jpg";
import Photo3 from "../assets/003.jpg";
import Photo4 from "../assets/004.jpg";
import Photo5 from "../assets/005.jpg";
import Photo6 from "../assets/006.jpg";
import Photo7 from "../assets/007.jpg";
import Photo8 from "../assets/008.jpg";
import Photo9 from "../assets/009.jpg";
import Photo10 from "../assets/010.jpg";
import Photo11 from "../assets/011.jpg";
import Photo12 from "../assets/012.jpg";
import Photo13 from "../assets/013.jpg";
import Photo14 from "../assets/014.jpg";
import Photo15 from "../assets/015.jpg";
import Photo16 from "../assets/016.jpg";
import Photo17 from "../assets/017.jpg";
import Photo18 from "../assets/018.jpg";
import Photo19 from "../assets/019.jpg";
import Photo20 from "../assets/020.jpg";
import Photo21 from "../assets/021.jpg";
import Photo22 from "../assets/022.jpg";
import Photo23 from "../assets/023.jpg";
import Photo24 from "../assets/024.jpg";
import Photo25 from "../assets/025.jpg";
import Photo26 from "../assets/026.jpg";
import Photo27 from "../assets/027.jpg";
import Photo28 from "../assets/028.jpg";
import Photo29 from "../assets/029.jpg";

const Wrapper = styled.div`
  padding-top: 42px;
  width: 70%;
  margin: 0 auto;
  item-align: center;
`;

const Title = styled.p`
  font-size: 1.25rem;
  color: var(--title-color);
  font-weight: bold;
  opacity: 0.85;
  margin-bottom: 0;
  text-align: center;
`;

const Gallery = () => {
  useEffect(() => {
    const root = document.querySelector("body, html");
    const container = document.querySelector(".gallery-wrapper");
    const images = document.querySelectorAll(".gallery-container > div > img");
    const l = images.length;

    for (var i = 0; i < l; i++) {
      images[i].addEventListener("click", function (i) {
        var currentImg = this;
        const parentItem = currentImg.parentElement,
          screenItem = document.createElement("div");
        screenItem.id = "screen";
        container.prepend(screenItem);
        if (parentItem.hasAttribute("data-theme"))
          screenItem.setAttribute("data-theme", "dark");
        var route = currentImg.src;
        root.style.overflow = "hidden";
        screenItem.innerHTML =
          '<div class="screen-img"></div><div class="screen-bottom"><div class="prev-btn screen-btn">&lsaquo;</div><div class="next-btn screen-btn">&rsaquo;</div><div class="close-btn screen-btn">&times</div></div>';
        const first = images[0].src,
          last = images[l - 1].src;
        const imgItem = document.querySelector(".screen-img"),
          prevBtn = document.querySelector(".prev-btn"),
          nextBtn = document.querySelector(".next-btn"),
          close = document.querySelector(".close-btn");
        imgItem.innerHTML = '<img src="' + route + '">';

        if (l > 1) {
          if (route == first) {
            prevBtn.hidden = true;
            var prevImg = false;
            var nextImg = currentImg.nextElementSibling;
          } else if (route == last) {
            nextBtn.hidden = true;
            var nextImg = false;
            var prevImg = currentImg.previousElementSibling;
          } else {
            var prevImg = currentImg.previousElementSibling;
            var nextImg = currentImg.nextElementSibling;
          }
        } else {
          prevBtn.hidden = true;
          nextBtn.hidden = true;
        }

        screenItem.addEventListener("click", function (e) {
          if (e.target == this || e.target == close) hide();
        });

        prevBtn.addEventListener("click", prev);
        nextBtn.addEventListener("click", next);

        function prev() {
          prevImg = currentImg.parentElement.previousElementSibling.firstChild;
          imgItem.innerHTML = '<img src="' + prevImg.src + '">';
          currentImg =
            currentImg.parentElement.previousElementSibling.firstChild;
          var mainImg = document.querySelector(".screen-img > img").src;
          nextBtn.hidden = false;
          prevBtn.hidden = mainImg === first;
        }

        function next() {
          nextImg = currentImg.parentElement.nextElementSibling.firstChild;
          imgItem.innerHTML = '<img src="' + nextImg.src + '">';
          currentImg = currentImg.parentElement.nextElementSibling.firstChild;
          var mainImg = document.querySelector(".screen-img > img").src;
          prevBtn.hidden = false;
          nextBtn.hidden = mainImg === last;
        }

        function hide() {
          root.style.overflow = "auto";
          screenItem.remove();
        }
      });
    }
  });

  return (
    <Wrapper>
      <Divider style={{ marginTop: 0, marginBottom: 32 }} plain>
        <Title>사진첩</Title>
      </Divider>
      <div class="gallery-wrapper">
        <div class="gallery-container">
          <div class="grid-item-1">
            <img src={Photo1} />
          </div>
          <div class="grid-item-2">
            <img src={Photo2} />
          </div>
          <div class="grid-item-3">
            <img src={Photo3} />
          </div>
          <div class="grid-item-1">
            <img src={Photo4} />
          </div>
          <div class="grid-item-2">
            <img src={Photo5} />
          </div>
          <div class="grid-item-3">
            <img src={Photo6} />
          </div>
          <div class="grid-item-1-2">
            <img src={Photo7} />
          </div>
          <div class="grid-item-2-2">
            <img src={Photo8} />
          </div>
          <div class="grid-item-3">
            <img src={Photo9} />
          </div>
          <div class="grid-item-1">
            <img src={Photo10} />
          </div>
          <div class="grid-item-2">
            <img src={Photo11} />
          </div>
          <div class="grid-item-3">
            <img src={Photo12} />
          </div>
          <div class="grid-item-1">
            <img src={Photo13} />
          </div>
          <div class="grid-item-2">
            <img src={Photo14} />
          </div>
          <div class="grid-item-3">
            <img src={Photo15} />
          </div>
          <div class="grid-item-1">
            <img src={Photo16} />
          </div>
          <div class="grid-item-2">
            <img src={Photo17} />
          </div>
          <div class="grid-item-3">
            <img src={Photo18} />
          </div>
          <div class="grid-item-1">
            <img src={Photo19} />
          </div>
          <div class="grid-item-2">
            <img src={Photo20} />
          </div>
          <div class="grid-item-3">
            <img src={Photo21} />
          </div>
          <div class="grid-item-1">
            <img src={Photo22} />
          </div>
          <div class="grid-item-2">
            <img src={Photo23} />
          </div>
          <div class="grid-item-3">
            <img src={Photo24} />
          </div>
          <div class="grid-item-1">
            <img src={Photo25} />
          </div>
          <div class="grid-item-2">
            <img src={Photo26} />
          </div>
          <div class="grid-item-3">
            <img src={Photo27} />
          </div>
          <div class="grid-item-1">
            <img src={Photo28} />
          </div>
          <div class="grid-item-2">
            <img src={Photo29} />
          </div>
        </div>
      </div>
    </Wrapper>
  );
};

export default Gallery;
