import React, { useState } from "react"
import styled from "styled-components"

const Wrapper = styled.div`
  padding-top: 22px;
  margin: 0 auto;
  width: 80%;
`;

const Header = styled.div`
  display: flex;
  justify-content: center;
  align-content: center;
  margin: 20px;

  .thisMonth {
    font-weight: 700;
    color: #292929;
    line-height: 24px;
  }
`

const Body = styled.div`
  text-align: center;
  justify-content: center;
  align-content: center;
  margin: 20px;

  .row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    width: 100%;
  }
  .row.week {
    height: 18px;
    border-bottom: 1px solid #e8e8e8;
  }
  .box {
    width: 26px;
    height: 26px;
    margin: 6px 3px;
    font-size: 14px;
  }
  .text {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 26px;
    height: 26px;
    color: #292929;
  }
  .day {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 26px;
    height: 26px;
  }
  .selected {
    width: 26px;
    height: 26px;
    border-radius: 50%;
    background: #444444;
    font-weight: 700;
    color: #fff;
  }
  .none {
    display: none;
  }
`

const Calendar = () => {
  //day
  const dayjs = require("dayjs")
  const weekday = require("dayjs/plugin/weekday")
  const isoWeek = require("dayjs/plugin/isoWeek")
  const weekOfYear = require("dayjs/plugin/weekOfYear")

  // day extend
  dayjs.extend(weekday)
  dayjs.extend(isoWeek)
  dayjs.extend(weekOfYear)

  const targetdate = dayjs("2025-03-01")

  const createCalendar = () => {
    const startWeek = targetdate.startOf("month").week()
    const endWeek =
      targetdate.endOf("month").week() === 1 ? 53 : targetdate.endOf("month").week()
    let calender = []

    for (let week = startWeek; week <= endWeek; week++) {
      calender.push(
        <div className="row" key={week}>
          {Array(7)
            .fill(0)
            .map((n, i) => {
              let current = targetdate
                .startOf("week")
                .week(week)
                .add(n + i, "day")
              if (targetdate.format("MM") === "12") {
                current = targetdate
                  .startOf("week")
                  .week(week - 52)
                  .add(n + i, "day")
              }
              let isSelected =
                targetdate.format("YYYYMMDD") === current.format("YYYYMMDD")
                  ? "selected"
                  : ""
              let isNone =
                current.format("MM") === targetdate.format("MM") ? "" : "none"
              return (
                <>
                  <div className={`box`} key={`${week}_${i}`}>
                    <div
                      className={`text ${isSelected} ${isNone}`}
                    >
                      <span className={`day`}>{current.format("D")}</span>
                    </div>
                  </div>
                </>
              )
            })}
        </div>
      )
    }
    return calender
  }

  return (
    <Wrapper>
      <Header>
        <span className="thisMonth">{targetdate.format("MM")}월</span>
      </Header>
      <Body>
        <div className="row week">
          <div className="box">
            <span className="text">일</span>
          </div>
          <div className="box">
            <span className="text">월</span>
          </div>
          <div className="box">
            <span className="text">화</span>
          </div>
          <div className="box">
            <span className="text">수</span>
          </div>
          <div className="box">
            <span className="text">목</span>
          </div>
          <div className="box">
            <span className="text">금</span>
          </div>
          <div className="box">
            <span className="text">토</span>
          </div>
        </div>
        <div>{createCalendar()}</div>
      </Body>
    </Wrapper>
  )
}

export default Calendar